'use strict';

/**
* Geometric distribution quantile function.
*
* @module @stdlib/stats-base-dists-geometric-quantile
*
* @example
* import quantile from '@stdlib/stats-base-dists-geometric-quantile';
*
* var y = quantile( 0.8, 0.4 );
* // returns 3
*
* y = quantile( 0.5, 0.4 );
* // returns 1
*
* var myquantile = quantile.factory( 0.4 );
* y = myquantile( 0.4 );
* // returns 0
*
* y = myquantile( 0.8 );
* // returns 3
*
* y = myquantile( 1.0 );
* // returns Infinity
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import factory from './factory.js';


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
